.banner{
/* margin-top: -10vh; */
margin-bottom: 0px;
  height:80vh;
  color:white;
  object-fit: contain;
  display: flex;
}
.banner_contents{

  margin-left: 30px;
  margin-top:100px;
}
.movie_title{
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner_discription{
  width: 45rem;
  line-height: 1;
  padding: 1rem 0;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}
.banner_button{
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  padding: 1rem 3rem;
  margin-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
}
.play{
  color: #000;
  background-color: #fff;
}
.banner_button:hover{
  color: #000;
  background-color: #c6c6c6;
  transition: all 0.2s;
}
.banner_fadebutton{
  .banner {
  position: relative; /* Enables child elements like .banner_fadebutton to position absolutely */
  margin-bottom: 0px;
  height: 80vh;
  color: white;
  object-fit: contain;
  display: flex;
}

.banner_fadebutton {
  position: absolute; /* Positioned relative to .banner */
  bottom: 0; /* Align to the bottom of the banner */
  width: 100%; /* Cover the full width of the banner */
  height: 20vh; /* Adjust as needed for the fade area */
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.91),
    #111
  );
  z-index: 1; /* Ensure it appears on top of background content */
}

